import crypto from "crypto"
export function encryptPass(password){
    let hash1 = crypto
    .createHash("md5")
    .update(password)
    .digest("hex");
    let pass = crypto
    .createHash("md5")
    .update(hash1)
    .digest("hex");
    return pass
}


