<template>
  <div>
    <CollapseButton icon="quote-left" :name="translate('Contents',lang)"></CollapseButton>
    <ContentFilter
      :lang="lang"
      class="collapse show"
      id="id_quote-left_element"
    ></ContentFilter>
    <CollapseButton icon="grin-beam" :name="translate('Feelings',lang)"></CollapseButton>
    <FeelingFilter
      :lang="lang"
      class="collapse show"
      id="id_grin-beam_element"
    ></FeelingFilter>
    <CollapseButton icon="air-freshener" :name="translate('Topics',lang)"></CollapseButton>
    <TopicFilter
      :lang="lang"
      :topics="userTopics"
      class="collapse show"
      id="id_air-freshener_element"
    ></TopicFilter>
  </div>
</template>

<script>
import CollapseButton from "./../../shared/CollapseButton";
import ContentFilter from "./ContentFilter";
import FeelingFilter from "./FeelingFilter";
import TopicFilter from "./TopicFilter";
import { translate } from "../../shared/translator";
export default {
  props: {
    lang: {
      type: String,
      default: "en"
    },
    userTopics: Array
  },
  data() {
    return {
      haveContentArr: [],
      haveFeelingArr: [],
      haveTopicArr: []
    };
  },
  components: {
    CollapseButton,
    TopicFilter,
    ContentFilter,
    FeelingFilter
  },
  methods: {
    translate: translate
  }
};
</script>

<style>
</style>



