<template>
  <div>
    <div v-if="isLoading" class="post-loading">
      <div class="row" style="width:100%; height:100%">
        <div class="col-2">
          <general-loading :color="'#c0f0fc'"></general-loading>
        </div>
        <div class="col-10">
          <post-loading></post-loading>
        </div>
      </div>
    </div>
    <div v-else>
      <div v-if="editing && index>=0" class="edit-post">
        <NewPostEditor
          :lang="lang"
          :editMode="true"
          :user="user"
          :post="post"
          v-on:edit_canceled="editing=false"
          v-on:post_updated="update_post"
        ></NewPostEditor>
      </div>
      <div v-else class="post">
        <MoreOptionDialog
          :lang="lang"
          v-if="index>=0"
          class="float-right"
          :post_id="post.id"
          :freezed="(post.status == 'freezed')"
          v-on:delete="$emit('delete_action', { id: post.id, index: index })"
          v-on:share="$emit('share_action', { id: post.id, index: index })"
          v-on:freeze="$emit('freeze_action', { id: post.id, index: index })"
          v-on:edit="editing=true"
        ></MoreOptionDialog>
        <div class="show-post">
          <div class="row align-items-start">
            <div class="col-md-auto">
              <img class="post-avatar" :src="user.avatar" />
            </div>
            <div class="col" style="padding-left:0px">
              <div class="row align-items-start">
                <div class="col-md-auto no-padding">
                  <span class="post-username">{{ user.name }}</span>
                </div>
                <div class="col-auto" v-show="post.status == 'freezed'">
                  <span class="input-group mt-1">
                    <icon
                      icon="snowflake"
                      size="sm"
                      class="tag-icon float-left mx-2"
                    />
                    <span class="mr-2">{{ translate(post.status,lang) }}</span>
                  </span>
                </div>
                <div v-if="post.topic.title != undefined" class="col">
                  <icon icon="tags" size="sm" class="tag-icon float-left mx-2" />
                  <span class="mr-2">{{translate(post.topic.title,lang)}}</span>
                </div>
              </div>

              <div class="row mt-2">
                <div class="col-md-auto">
                  <span class="input-group">
                    <icon
                      icon="clock"
                      pull="left"
                      size="xs"
                      class="post-icons input-group-prepend mt-1"
                    />
                    <p class="mini-text">{{ post.time }}</p>
                  </span>
                </div>
                <div class="col align-self-center" v-show="post.location">
                  <span class="input-group">
                    <icon
                      icon="map-marked-alt"
                      pull="left"
                      size="xs"
                      class="post-icons input-group-prepend"
                    />
                    <p class="mini-text">{{ post.location }}</p>
                  </span>
                </div>
              </div>
            </div>
          </div>
          <hr />
          <div class="row">
            <img class="col-auto" :src="feelingArr[parseInt(post.feeling)]" alt />
            <p v-show="post.title.length" class="col-auto post-content-text mt-3">{{ post.title }}</p>
          </div>
          <div class="grid-container">
            <img
              v-for="(image, ii)  in post.images"
              v-bind:key="'image'+ii"
              :class="'mborder '+((ii==0 || post.images.length <=2)?'single-image':((post.images.length >3)?'grid-image-tripple':'grid-image-double'))"
              :src="image"
              v-show="ii<4"
              v-on:click="onClick(ii)"
            />
            <audio
              style="display:inline-block; outline:none !important; border:none; width:100%"
              v-for="(audio, vi)  in post.audios"
              v-bind:key="'audio'+vi"
              controls
            >
              <source :src="audio" type="audio/ogg" />
              <source src="horse.mp3" type="audio/mpeg" />Your browser does not support the audio element.
            </audio>
            <video
              v-for="(video, vi)  in post.videos"
              v-bind:key="'video'+vi"
              :class="'mborder '+((vi==0 || post.videos.length <=2)?'single-image':((post.videos.length >3)?'grid-image-tripple':'grid-image-double'))"
              controls
            >
              <source :src="video" />
              <source src="movie.ogg" type="video/ogg" />Your browser does not support the video tag.
            </video>
          </div>
          <vue-gallery-slideshow
            :images="post.images"
            :index="image_index"
            @close="image_index = null"
          ></vue-gallery-slideshow>
          <span></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import VueGallerySlideshow from "vue-gallery-slideshow";
import GoodActive from "../../shared/assets/good_active.png";
import HateActive from "../../shared/assets/hate_active.png";
import NaturalActive from "../../shared/assets/natural_active.png";
import ExcellentActive from "../../shared/assets/excellent_active.png";
import DisappointedActive from "../../shared/assets/disappointed_active.png";
import NewPostEditor from "../new_post_components/NewPostEditor";
import PostLoading from "../../shared/PostLoading";
import GeneralLoading from "../../shared/GeneralLoading";
import { translate } from "../../shared/translator";

import {
  faClock,
  faMapMarkedAlt,
  faUserLock
} from "@fortawesome/free-solid-svg-icons";

library.add(faClock, faMapMarkedAlt, faUserLock);
import MoreOptionDialog from "./MoreOptionDialog";
import { EventBus } from "../../repositories/event-bus";
import postRepository from "../../../repositories/postRepository";
import { errorMessages } from "../../shared/errorMessage";
export default {
  name: "post",
  async created() {
    EventBus.$on("edit_called", postID => {
      if (this.post._id === postID) this.editing = true;
    });
    if (this.index != -1) {
      //xem ở chế dộ bình thường
      let id = this.postID;
      try {
        this.isLoading = true;
        this.post = await postRepository.getPost(id);
        console.log("post",this.post)
      } catch (error) {
        console.log("loadpost bị lỗi", error);
        EventBus.$emit("showError", { message: errorMessages['load_post_error'] });
      } finally {
        this.isLoading = false;
      }
    }else{
      this.post=this.share_post;
    }
  },
  data() {
    return {
      post: {},
      editing: false,
      feelingArr: [
        ExcellentActive,
        GoodActive,
        NaturalActive,
        DisappointedActive,
        HateActive
      ],
      image_index: null,
      security: "Bí mật",
      images: this.getImageScr,
      isLoading: false
    };
  },
  props: {
    postID: String,
    index: Number,
    share_post: {},
    user: Object,
    lang: { type: String, default: "en" }
  },
  components: {
    MoreOptionDialog,
    VueGallerySlideshow,
    NewPostEditor,
    PostLoading,
    GeneralLoading
  },
  methods: {
    translate: translate,
    onClick(i) {
      this.image_index = i;
    },
    async update_post(e) {
      console.log("update content form post ", e);
      this.editing = false;
      console.log("post_ old", this.post);
      try {
        this.isLoading = true;
        await postRepository.updatePost(
          this.post._id,
          e.textContents,
          e.mediaContents
        );
        console.log("cập nhật thành công");
        this.post = await postRepository.getPost(this.post._id);
        console.log("post lấy từ server về:", this.post);
      } catch (e) {
        console.log("cập nhập thất bại", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    }
  }
};
</script>

<style>
.post {
  border-radius: 32px;
  background-color: white;
  border: solid 1px rgb(177, 177, 177);
  padding: 20px;
}

.post-icons {
  color: black;
  /* width: 40px; */
  /* height: 40px; */
  /* margin: 10px; */
  /* padding: 2px; */
  /* border: solid 0px; */
  /* border-radius: 32px; */
}
.single-image {
  width: 100%;
  padding: 10px;
  height: auto;
}
.grid-image-double {
  width: 50%;
  padding: 10px;
  /* display: grid;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
  grid-gap: 1rem;
  max-width: 100%;
  margin: 1rem auto;
  padding: 0 5rem; */
}
.grid-image-tripple {
  width: 30%;
  margin: 10px;
  /* display: grid;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
  grid-gap: 1rem;
  max-width: 100%;
  margin: 1rem auto;
  padding: 0 5rem; */
}
.media-content {
  width: auto;
  border-radius: 32px;
  height: auto;
  object-fit: cover;
  border-radius: 0.75rem;
  display: inline-block;
}
.post-username {
  color: black;
}
.mini-text {
  font-size: 12px;
}
.post-avatar {
  border-radius: 32px;
  width: 40px;
  height: 40px;
}
.post-content-text {
  background-color: rgb(203, 226, 247);
  border-bottom-right-radius: 32px;
  border-bottom-left-radius: 32px;
  border-top-right-radius: 32px;
  border: 1px;
  padding: 10px;
  text-align: center;
  vertical-align: auto;
}
</style>



