<template>
  <ul class="list-group-sm mr-2 my-block">
    <li class="list-group-item" v-for="(device, i) in devices" v-bind:key="i">
      <div class="row">
        <icon
          class="col-auto mt-2"
          v-bind:icon="device.type == 'mobile' ? 'tablet-alt' : 'desktop'"
          pull="right"
          size="lg"
        />
        <div class="col">
          <button class="btn btn-sm btn-primary float-right mborder" @click="logoutDevice(i)">
            {{ logout_str }}
            <icon class="mt-1" icon="sign-out-alt" pull="right" size="md" />
          </button>
        </div>
      </div>
      <span>{{ device.name }}</span>
      <p v-if="device.thisDevice" style="color:red">{{active_device_str}}</p>
      <p v-if="!device.thisDevice" class="mini-text">({{ last_active_str }}{{ device.last_active }}, {{translate('at',lang)}} :{{device.location}})</p>
    </li>
  </ul>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faTabletAlt,
  faDesktop,
  faSignOutAlt
} from "@fortawesome/free-solid-svg-icons";
import serviceRepository from "../../../repositories/serviceRepository"
import userRepository from "../../../repositories/userRepository"
import { translate } from "../../shared/translator";
library.add(faTabletAlt, faDesktop, faSignOutAlt);
export default {
  async created() {
    console.log(this.ip)
    this.devices = [];
    for(let i=0 ; i< this.list_devices.length; i++){
      let de = this.list_devices[i];
      let type = de.info.search("Android") >= 0 ? "mobile" : "desktop";
      let last_active = new Date(de.lastAccess).toLocaleString("vi");
      let location= await serviceRepository.getLocationFromIp(de.ip);
      console.log("location: ",location);
      this.devices.push({
        name: de.info,
        type: type,
        last_active: last_active,
        location:location,
        thisDevice:(de.ip==this.ip)
      });
    };
  },
  data() {
    return {
      logout_str: translate("Logout",this.lang),
      last_active_str: translate("last_active",this.lang),
      active_device_str:translate("this_device",this.lang),
      devices: []
    };
  },
  props: {
    lang:{
      type:String,
      default:"en"
    },
    list_devices: Array,
    ip:String
  },
  methods:{
    translate:translate,
    async logoutDevice(index){
      console.log("index ",this.list_devices[index].ip)
      try {
        this.$emit("loading")
        let result = await userRepository.logoutDevice(this.list_devices[index].ip);
        if(this.devices[index].thisDevice) this.$emit('logout');
        this.devices.splice(index,1);
      } catch (error) {
        window.alert("Lỗi đăng xuất khỏi thiết bị, vui lòng thử lại")
      }finally{
        this.$emit("loading")
      }
    }
  }
};
</script>

<style>
.list-group-item {
  border: none;
  margin-bottom: 0;
}
</style>



