<template>
  <div :key="myAppKey">
    <AuthenticationPopup
      :full_screen="true"
      :lang="lang"
      :show="asking_password"
      title="app_password_required"
      :password="appPassword"
      v-on:correct="asking_password=false"
      v-on:incorrect="handleIncorrect"
    ></AuthenticationPopup>
    <button hidden id="showInfoModalBtn" data-toggle="modal" data-target="#infoModal"></button>
    <InfoPopup :lang="lang" :message="popupMessage" @ok_clicked="reloadPage"></InfoPopup>
    <LoginScreen :lang="lang" v-if="!isLoged" v-on:logged_in="change_user" @showPopup="showPopup"></LoginScreen>
    <div v-if="isLoged">
      <NavigatorBar
        :lang="lang"
        class="sticky-top"
        v-on:myEvent="change_extension_tab"
        :user="user"
        @search="searchPost"
      ></NavigatorBar>
      <div class="container-fluid">
        <div class="row my-2">
          <div class="col-lg-3 col-md-4 col-sm-none">
            <Calendar :lang="lang"></Calendar>
          </div>
          <div class="col-lg-6 col-md-4 col-sm-8">
            <Body
              :lang="lang"
              ref="body"
              :user="user"
              @showPopup="showPopup"
            ></Body>
          </div>
          <div class="col-lg-3 col-md-4 col-sm-6">
            <ExtensionTab
              :lang="lang"
              v-bind:active_tab="active_tab"
              :user="user"
              @logout="logout"
              @showPopup="showPopup"
            ></ExtensionTab>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Calendar from "./components/calendar/calendar.vue";
import ExtensionTab from "./components/extensions/ExtensionTab.vue";
import NavigatorBar from "./components/navigationbar/NavigatorBar";
import LoginScreen from "./components/LoginScreen";
import Body from "./components/body/Body.vue";
import { getCookie, setCookie } from "./repositories/cookieRepository";
import userRepository from "./repositories/userRepository";
import Loading from "vue-loading-overlay";
import InfoPopup from "./components/shared/InfoPopup";
import { timeout } from "q";
import { EventBus, SharedStorage } from "./components/repositories/event-bus";
import AuthenticationPopup from "./components/shared/AuthenticationPopup";
import { errorMessages } from "./components/shared/errorMessage";

export default {
  name: "app",
  data() {
    return {
      active_tab: 0,
      isLoged: false,
      user: {},
      popupMessage: {},
      myAppKey: 0,
      lang: "",
      asking_password:
        SharedStorage.settings.securitySettings.appPassword.turnOn,
      appPassword: SharedStorage.settings.securitySettings.appPassword.password,
      wrong_pass_count: 0
    };
  },
  async beforeCreate() {
    this.active_tab = 0;
    EventBus.$on("change_language", lang => {
      console.log("change language", lang);
      this.lang = lang;
      this.active_tab = 0;
      this.myAppKey++;
      setCookie("lang", lang, 1000);
    });
    EventBus.$on("showError", message => {
      console.log("show Info",message);
      
      this.showPopup({ message: message });
    });
    EventBus.$on("force_logout", async () => {
      await this.logout();
    });
  },
  async created() {
    //đảm bảo không vào tab setting đầu tiên
  },
  beforeMount() {
    if (getCookie("lang") != null) this.lang = getCookie("lang");
    else this.lang = "en";
  },

  components: {
    Calendar,
    ExtensionTab,
    NavigatorBar,
    Body,
    LoginScreen,
    Loading,
    InfoPopup,
    AuthenticationPopup
  },
  methods: {
    change_extension_tab(e) {
      console.log(e);
      if (e.tab_id == "settings") {
        this.active_tab = 2;
      } else if (e.tab_id == "profile") {
        this.active_tab = 1;
      } else this.active_tab = 0; //filter
    },
    async change_user(e) {
      console.log("logged success?", e);
      this.user = e;
      this.isLoading = true;
      try {
        SharedStorage.settings = await userRepository.getUserSettings();
        this.asking_password =
          SharedStorage.settings.securitySettings.appPassword.turnOn;
        this.appPassword =
          SharedStorage.settings.securitySettings.appPassword.password;
        this.isLoged = true;
      } catch (e) {
        console.log(e);
        //TODO xử lý lỗi nếu không load được settings
        let message = errorMessages[`${e}`];
        this.$emit("showPopup", { message: message });
      } finally {
        this.isLoading = false;
      }
      // this.$refs.body.refreshPosts();
    },
    async logout() {
      await userRepository.logout();
      this.isLoged = false;
    },
    searchPost(e) {
      console.log(e);
      this.currentFilter.title = e;
      this.$refs.body.refreshPosts(this.currentFilter);
    },
    showPopup(e) {
      document.getElementById("showInfoModalBtn").click();
      console.log("show popup");
      if(e.message != undefined && e.message!=null)
      this.popupMessage = e.message;
      else this.popupMessage = errorMessages[0]
      console.log(this.popupMessage);
    },
    reloadPage() {
      console.log("reload page");
      this.myAppKey++;
    },

    handleIncorrect() {
      this.wrong_pass_count++;
      if (this.wrong_pass_count > 4) {
        this.showPopup({ message: errorMessages[401] });
        this.logout();
        this.wrong_pass_count = 0;
      }
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



