<template>
  <div class="extensiontab">
    <div v-if="isLoading" class="extention-loading mborder">
      <general-loading class="float-right mr-5" :color="'#c0f0fc'"></general-loading>
    </div>
    <div class="row">
      <div class="col-6">
        <h4>{{ tabs[active_tab].tab_name }}</h4>
      </div>
      <div class="col-6">
        <button
          v-on:click="updateExtension"
          v-if="tabs[active_tab].btn_fn_name != ''"
          class="btn btn-primary btn-function float-right"
        >{{ tabs[active_tab].btn_fn_name }}</button>
      </div>
    </div>
    <hr class="mr-3" />
    <Filters
      :lang="lang"
      class="scroll_extensions"
      v-if="active_tab == 0"
    ></Filters>
    <Profile
      :lang="lang"
      class="scroll_extensions"
      v-if="active_tab == 1"
      :user="user"
      @logout="$emit('logout')"
      @avatar_changed="change_avatar"
      @profile_changed="change_profile"
    ></Profile>
    <Setting
      :lang="lang"
      class="scroll_extensions"
      v-if="active_tab == 2"
      :user="user"
      :userSettings="settings"
      @loading="isLoading=!isLoading"
      @logout="$emit('logout')"
      @update_setting="change_settings"
    ></Setting>
  </div>
</template>

<script>
import Profile from "./Profile";
import Filters from "./filters/Filters";
import Setting from "./settings/Setting";
import userRepository from "../../repositories/userRepository";
import postRepository from "../../repositories/postRepository";
import { translate } from "../shared/translator.js";
import { errorMessages } from "../shared/errorMessage";
import { EventBus, SharedStorage } from "../repositories/event-bus";
import GeneralLoading from "../shared/GeneralLoading";
export default {
  components: {
    Profile,
    Setting,
    Filters,
    GeneralLoading
  },
  // async beforeCreate() {
  //   this.isLoading = true;
  //   try {
  //     this.settings = await userRepository.getUserSettings();
  //     this.$emit(
  //       "update_app_password",
  //       this.settings.securitySettings.appPassword
  //     );
  //     EventBus.$emit("topic_changed", this.settings.topicSettings.topics);
  //     EventBus.$emit("freeze_setting_changed", this.settings.freezeSettings);
  //     SharedStorage.topics = this.settings.topicSettings.topics;
  //     console.log(this.settings);
  //   } catch (e) {
  //     console.log(e);
  //     //TODO xử lý lỗi nếu không load được settings
  //     let message = errorMessages[`${e}`];
  //     this.$emit("showPopup", { message: message });
  //   } finally {
  //     this.isLoading = false;
  //   }
  // },
  data() {
    return {
      tabs: [
        {
          tab_name: translate("Filters", this.lang),
          btn_fn_name: translate("Apply", this.lang)
        },
        {
          tab_name: translate("Profile", this.lang),
          btn_fn_name: translate("Update", this.lang)
        },
        {
          tab_name: translate("Settings", this.lang),
          btn_fn_name: ""
        }
      ],
      isLoading: false,
      profile_temp: {},
      avatar_file: "",
      filter: {},
      topics: SharedStorage.settings.topicSettings.topics,
      // settings: { topicSettings: { topics: [] }, securitySettings: {} }
      settings: SharedStorage.settings
    };
  },
  watch: {
    settings: function(val) {
      console.log("settings đã load về:", val);
      this.topics = val.topicSettings.topics;
    }
  },
  props: {
    active_tab: 0,
    user: Object,
    lang: {
      type: String,
      default: "en"
    }
  },
  methods: {
    translate: translate,
    async updateExtension() {
      console.log("cập nhập ở ", this.active_tab);
      this.isLoading = true;
      if (this.active_tab === 1) {
        //cập nhập profile
        try {
          await userRepository.updateProfile(
            this.profile_temp,
            this.avatar_file
          );
        } catch (e) {
          console.log(e);
        } finally {
          this.isLoading = false;
        }
      } else if (this.active_tab === 0) {
        // this.$emit("apply_filter", this.filter);
        EventBus.$emit('filter_changed')
        this.isLoading = false;
      } else if (this.active_tab === 2) {
        try {
          this.isLoading = true;
          console.log(this.settings);
          let r = await userRepository.updateUserSettings(this.settings);
          let old_freezeShow_value= SharedStorage.settings.freezeSettings.showFreezeDiary;
          SharedStorage.settings = this.settings;
          let new_freezeShow_value= SharedStorage.settings.freezeSettings.showFreezeDiary;
          if(old_freezeShow_value != new_freezeShow_value){
            EventBus.$emit("freezeSetting_changed");
          }
          // EventBus.$emit("topic_changed", this.settings.topicSettings.topics);
          EventBus.$emit("setting_changed");
          console.log("update xong");
        } catch (e) {
          console.log(e);
          //TODO thông báo update thất bại
          let message = errorMessages[`${e}`];
          this.$emit("showPopup", { message: message });
        } finally {
          this.isLoading = false;
        }
      }
    },
    change_avatar(file) {
      this.avatar_file = file;
    },
    change_profile(profile) {
      this.profile_temp = profile;
    },
    async change_settings(setting) {
      // console.log("settings changed");
      this.settings = setting;
      await this.updateExtension();
    }
  }
};
</script>

<style>
.extention-loading {
  position: absolute;
  width: inherit;
  height: inherit;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(
    255,
    255,
    255,
    0.3
  ); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: none; /* Add a pointer on hover */
  text-align: center;
  padding-top: 10pt;
}
.extensiontab {
  border-radius: 32px;
  background-color: whitesmoke;
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  height: 90vh;
  /* margin-right: 200px; */
  /* padding-right: 50px; */
}
.scroll_extensions {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 80vh;
}
.scroll_extensions::-webkit-scrollbar {
  width: 3px;
}

.scroll_extensions::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 32px;
}

.scroll_extensions::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 32px;
}

.scroll_extensions::-webkit-scrollbar-thumb:hover {
  background: red;
}
.btn-function {
  border-radius: 32px;
  margin-right: 10px;
}
.btn-function:hover {
  border-radius: 32px;
  margin-right: 10px;
}
.btn-function:active {
  border-radius: 32px;
  margin-right: 10px;
}
</style>



