<template>
  <div class="scroll_body">
    <button hidden id="showDeleteModalBtn" data-toggle="modal" data-target="#deleteModal"></button>
    <button hidden id="showShareModalBtn" data-toggle="modal" data-target="#shareModal"></button>
    <button hidden id="showFreezeModalBtn" data-toggle="modal" data-target="#freezeModal"></button>
    <DeletePostModal
      :lang="lang"
      v-on:delete="delete_post"
      v-on:freeze="callDeleteAction();callFreezeAction()"
      v-on:edit="callDeleteAction(),callEditAction()"
    ></DeletePostModal>
    <SharePostModal
      :lang="lang"
      v-if="posts.length"
      v-on:commit_share="share_post"
      :shareLink="posts[active_index].shareLink"
      :shareOn="posts[active_index].shareLinkStatus"
      @updateShareLinkStatus="updateShareLinkStatus"
    ></SharePostModal>
    <FreezePostModal :lang="lang" :freeze="isFreezeAction" @freeze="freeze_post"></FreezePostModal>
    <NewPostEditor
      :lang="lang"
      class="shadow"
      :user="user"
      @new_post_created="refreshPosts"
      :editMode="false"
    ></NewPostEditor>
    <div v-if="isLoading">
      <div v-for="(l,i) in [0]" :key="i" class="body-loading my-3">
        <div class="row" style="width:100%; height:100%">
          <div class="col-2">
            <general-loading :color="'#c0f0fc'"></general-loading>
          </div>
          <div class="col-10">
            <post-loading></post-loading>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="my-3" v-for="(post, index) in posts" v-bind:key="index">
        <Post
          :lang="lang"
          v-if="posts.length"
          :index="index"
          :postID="post._id"
          v-on:delete_action="callDeleteAction"
          v-on:freeze_action="callFreezeAction"
          v-on:share_action="callShareAction"
          v-on:edit_action="callEditAction"
          :user="user"
        />
      </div>
    </div>
  </div>
</template>

<script>
import NewPostEditor from "./new_post_components/NewPostEditor";
import Post from "./post_components/Post";
import postRepository from "../../repositories/postRepository";
import DeletePostModal from "./post_components/DeletePostModal";
import SharePostModal from "./post_components/SharePostModal";
import FreezePostModal from "./post_components/FreezePostModal";
import Loading from "vue-loading-overlay";
import GeneralLoading from "../shared/GeneralLoading";
import { errorMessages } from "../shared/errorMessage";
import Logo from "../shared/assets/logo.png";
import { EventBus, SharedStorage } from "../repositories/event-bus";
import PostLoading from "../shared/PostLoading";

export default {
  props: {
    lang: {
      type: String,
      default: "en"
    },
    filter: Object,
    user: Object
  },
  data() {
    return {
      isLoading: false,
      active_index: 0,
      posts: [],
      logo: Logo,
      isFreezeAction: false
    };
  },
  components: {
    NewPostEditor,
    Post,
    DeletePostModal,
    FreezePostModal,
    SharePostModal,
    GeneralLoading,
    PostLoading
  },

  async created() {
    // try {
    //   this.isLoading = true;
    //   let result = await postRepository.getListPost(0);
    //   console.log(result);
    //   this.posts = result.posts;
    // } catch (e) {
    //   console.log(e);
    //   let message = errorMessages[`${e}`];
    //   this.$emit("showPopup", { message: message });
    // } finally {
    //   this.isLoading = false;
    // }
    EventBus.$on('filter_changed',async ()=>{
      await this.refreshPosts();
    })
    EventBus.$on('freezeSetting_changed', async()=>{
      await this.refreshPosts();
    })
    this.refreshPosts();
  },
  methods: {
    callDeleteAction(e) {
      //hàm chỉ có tác dụng gọi modal xác thực
      console.log(e);
      document.getElementById("showDeleteModalBtn").click();
      if (e != undefined && e.index != undefined) this.active_index = e.index;
    },
    callEditAction(e) {
      console.log(e);
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      console.log("edit in ", this.active_index);
      EventBus.$emit("edit_called", this.posts[this.active_index]._id);
      //hàm chỉ có tác dụng gọi modal xác thực
      // document.getElementById("showDeleteModalBtn").click();
      // this.active_index = e.index;
    },
    callShareAction(e) {
      console.log(e);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showShareModalBtn").click();
    },
    callFreezeAction(e) {
      //nếu ở chế độ hiển thị nhật ký đóng băng thì action sẽ là warm_up-> ngược lại
      // this.isFreezeAction = !SharedStorage.settings.freezeSettings.showFreezeDiary;
      // console.log(this.isFreezeAction);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showFreezeModalBtn").click();
    },
    async share_post(e) {
      //đóng modal
      document.getElementById("showShareModalBtn").click();
      try {
        let listEmail = e.emails;
        console.log(listEmail);
        let listSuccessSent = await postRepository.share_post_to_email(
          this.posts[this.active_index]._id,
          listEmail
        );
        console.log(listSuccessSent);
        //TODO hiển thị danh sách email đã gửi thành công
      } catch (e) {
        console.log(e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      }
    },
    async updateShareLinkStatus(e) {
      try {
        this.isLoading = true;
        console.log("status", e.status);
        await postRepository.updatePostShareLinkStatus(
          this.posts[this.active_index]._id,
          e.status.checked
        );
        this.posts[this.active_index].shareLinkStatus = status;
      } catch (e) {
        console.log("lỗi bật/tắt link share", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    },
    async freeze_post() {
      //đóng modal
      document.getElementById("showFreezeModalBtn").click();
      try {
        this.isLoading = true;
        console.log("Làm nóng", this.posts[this.active_index]._id);
        await postRepository.freezePost(
          this.posts[this.active_index]._id,
          !SharedStorage.settings.freezeSettings.showFreezeDiary
        );
        this.posts.splice(this.active_index, 1);
      } catch (e) {
        console.log("lỗi đóng làm nóng niệm", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    },
    async delete_post() {
      //đóng modal
      document.getElementById("showDeleteModalBtn").click();
      console.log("triggle delete");
      try {
        this.isLoading = true;
        await postRepository.deletePost(this.posts[this.active_index]._id);
        this.posts.splice(this.active_index, 1);
      } catch (e) {
        console.log("lỗi xóa kỉ niệm", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    },
    async refreshPosts(filter = null) {
      try {
        console.log(SharedStorage.settings.freezeSettings);
        
        this.isLoading = true;
        // filter.includeFreezedPost=SharedStorage.settings.freezeSettings.showFreezeDiary;
        let result = await postRepository.getListPost(0, SharedStorage.currentFilter);
        this.posts = result.posts;
      } catch (e) {
        console.log(e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    }
  }
};
</script>

<style>
.scroll_body {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 90vh;
}
.scroll_body::-webkit-scrollbar {
  width: 0px;
}
/* 
.scroll_body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 32px;
}
 
.scroll_body::-webkit-scrollbar-thumb {
  background: gray; 
  border-radius: 32px;
}

.scroll_body::-webkit-scrollbar-thumb:hover {
  background:red; 
} */
</style>



