import Vue from 'vue'
import Router from 'vue-router'
import App from './App.vue'
import babelPolyfill from 'babel-polyfill'
import VueAxios from 'vue-axios'
import VueSocialauth from 'vue-social-auth'
import axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import TextareaAutosize from 'vue-textarea-autosize'
import VueRecord from '@codekraft-studio/vue-record'
import AppRouter from './AppRouter.vue'
import ShareScreen from './components/ShareScreen.vue'
import ErrorScreen from './components/ErrorScreen.vue'
import InfoPopup from "./components/shared/InfoPopup"
import DateRangePicker from 'vue-tql-daterangepicker'
import Loading from "vue-loading-overlay";
import 'vue-tql-daterangepicker/dist/vue-tql-daterangepicker.css'
import { LoaderPlugin } from 'vue-google-login';
Vue.use(LoaderPlugin, {
  client_id: "571858267778-fa8fu87hpjoel1svuevnpjm94s4oeev2.apps.googleusercontent.com",
  client_secret:"AybaKEwNFbTVIXchlvSPRqHv"
});
Vue.use(Loading)
Vue.use(DateRangePicker)
Vue.use(Router)
Vue.component('icon', FontAwesomeIcon)
var router =new Router({
    routes: [
      {
        path: '/share',
        name: 'Share',
        component: ShareScreen
      },
      {
        path: '/',
        name: 'QDIARY',
        component: App
      },
      {
        path: '/error',
        name: 'NOTFOUND',
        component: ErrorScreen
      },
      {
        path: '*',
        redirect:'/error'
      },
    ]
  })
Vue.config.productionTip = false
new Vue({
  el: '#app',
  router,
  render: h => h(AppRouter)
})
Vue.use(VueRecord)
Vue.use(VueAxios, axios)
Vue.use(babelPolyfill)
Vue.use(TextareaAutosize)
Vue.use(VueSocialauth, {
  providers: {
    github: {
      clientId: '',
      redirectUri: '/auth/github/callback' // Your client app URL
    },
    google:{
     
        clientId:"571858267778-ogimpuu5ekdli8o3u4o4qbe32o90dtpj.apps.googleusercontent.com",
        project_id:"qdiary-1574419423624",
        auth_uri:"https://accounts.google.com/o/oauth2/auth",
        token_uri:"https://oauth2.googleapis.com/token",
        auth_provider_x509_cert_url:"https://www.googleapis.com/oauth2/v1/certs",
        client_secret:"o8AGu8jSDbWnqwWpN9iYXl0l",
        javascript_origins:["http://localhost:8080"],
        redirectUri: 'http://localhost:8080' 
    }

    
  }
})


