import {setCookie, deleteAllCookies,getCookie} from "./cookieRepository"
import axios from 'axios';
import 'babel-polyfill'
const ip_api = "https://ipapi.co/";
export default {
    async getLocationFromIp(ip){
        try {
            let location = await axios.get(ip_api+ip+"/city");
            console.log(location);
            return Promise.resolve(location.data)
        } catch (error) {
            return Promise.resolve("Unknow Location");   
        }
    }
}


