<template>
  <div class="my-block mr-2">
    <!-- <span>Chứa đựng:</span> -->
    <ul class="list-group-sm no-padding-inline">
      <li class="list-group-item" @click="content.checked= ! content.checked" v-for="(content,index) in contents" v-bind:key="index">
        {{translate(content.alias,lang)}}
        <!-- <input
          v-model="content.checked"
          type="checkbox"
          class="float-right"
          @change="updateContentFilter"
        /> -->
        <CheckboxElement
          v-model="content.checked"
          class="float-right mr-4"
          :disabled="true"
          @change="updateContentFilter"
        ></CheckboxElement>
      </li>
    </ul>
  </div>
</template>

<script>
import { translate } from "../../shared/translator";
import CheckboxElement from "../../shared/CheckboxElement";
import { SharedStorage } from '../../repositories/event-bus';
export default {
  components: {
    CheckboxElement
  },
  props: {
    lang: {
      type: String,
      default: "en"
    }
  },
  data() {
    return {
      contents: [
        {
          name: "Hình ảnh",
          checked: false,
          alias: "Images"
        },
        {
          name: "Video",
          checked: false,
          alias: "Videos"
        },
        {
          name: "Văn bản",
          checked: false,
          alias: "Texts"
        },
        {
          name: "Đoạn ghi âm",
          checked: false,
          alias: "Audios"
        }
      ]
    };
  },
  methods: {
    translate: translate,
    updateContentFilter() {
      let contentArr = [];
      for (let i = 0; i < this.contents.length; i++) {
        if (this.contents[i].checked == true)
          contentArr.push(this.contents[i].alias);
      }
      SharedStorage.currentFilter.contents=contentArr;
    }
  }
};
</script>

<style>
.list-group-item {
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding-right: 20px;
}
</style>


