<template>
  <div class="my-block">
    <!-- <span>Cảm xúc</span> -->
    <div class="row mx-2">
      <div class="col col-icon" v-for="(feel,i) in  feels" v-bind:key="i">
        <div class="feel-info">
          <!-- <span class="num-feels">{{feel.num_post}}</span> -->
          <!-- <input type="checkbox" name v-bind:id="'feelcheck'+i" v-model="feel.status" @change="updateFeelingFilter"/> -->
          <CheckboxElement class="ml-1" v-bind:id="'feelcheck'+i" v-model="feel.status" @change="updateFeelingFilter"></CheckboxElement>
        </div>
        <img class="icon-feeling" v-bind:src="feel.icon" @click="feel.status=!feel.status" />
      </div>
    </div>
  </div>
</template>

<script>
import GoodActive from "../../shared/assets/good_active.png";
import HateActive from "../../shared/assets/hate_active.png";
import NaturalActive from "../../shared/assets/natural_active.png";
import ExcellentActive from "../../shared/assets/excellent_active.png";
import DisappointedActive from "../../shared/assets/disappointed_active.png";
import CheckboxElement from "../../shared/CheckboxElement";
import { SharedStorage } from '../../repositories/event-bus';
export default {
  props:{
    lang:{
      type:String,
      default:"en"
    },
  },
  components:{
    CheckboxElement
  },
  data() {
    return {
      feels: [
        {
          icon: ExcellentActive,
          num_post: 233,
          status: false
        },
        {
          icon: GoodActive,
          num_post: 636,
          status: false
        },
        {
          icon: NaturalActive,
          num_post: 453,
          status: false
        },
        {
          icon: DisappointedActive,
          num_post: 23,
          status: false
        },
        {
          icon: HateActive,
          num_post: 231,
          status: false
        }
      ]
    };
  },
  methods:{
    updateFeelingFilter(){
      console.log("update feeling in FeelingFilter.vue")
      let feelingArr=[];
      for(let i=0; i< this.feels.length; i++){
        if(this.feels[i].status==true) feelingArr.push(i+'');
      }
      console.log(feelingArr);
      SharedStorage.currentFilter.feelings=feelingArr;
    }
  }
};
</script>

<style>
.icon-feeling {
  width: 50px;
  /* margin-left: 5px; */
  cursor: pointer;
}
.num-feels {
  font-size: 10px;
  color: red;
}
.col-icon {
  padding: 0 !important;
  margin: 0 !important;
  justify-content: space-between;
}
.feel-info {
  margin-left: 40px;
}
</style>



