<template>
  <div v-if="this.topics.length>0" class="my-block mr-2">
    <!-- <span>Chủ đề</span> -->
    <div v-for="(topic,i) in topics" v-bind:key="i">
      <TopicWithBackground :lang="lang" :topic="topic" @changed="updateTopicFilter"></TopicWithBackground>
    </div>
  </div>
</template>

<script>
  import TopicWithBackground from "../../shared/TopicWithBackground";
import { SharedStorage, EventBus } from '../../repositories/event-bus';
export default {
  components:{
    TopicWithBackground
  },
  created(){
    EventBus.$on('setting_changed',()=>{
      this.topics=SharedStorage.settings.topicSettings.topics;
    })
  },
  data() {
    return {
      topics: SharedStorage.settings.topicSettings.topics,
      topicChecked: []
    };
  },
  props: {
    lang:{
      type:String,
      default:"en"
    },
  },
  methods: {
    updateTopicFilter(e) {
      if (e.checked) {
        this.topicChecked.push(e.topicID);
      } else {
        for (let i = 0; i < this.topicChecked.length; i++) {
          if (this.topicChecked[i] === e.topicID) {
            this.topicChecked.splice(i, 1);
            break;
          }
        }
      }
      SharedStorage.currentFilter.topics=this.topicChecked;
    }
  }
};
</script>

<style>

</style>


