<template>
  <div>
    <span>{{ avatar_label }}</span>
    <br />
    <div class="row my-2">
      <div class="col"></div>
      <div class="col-auto">
        <img :src="avatar_source" class="avatar-profile rounded-circle" />
        <input @change="change_avatar" ref="avatar_edit" type="file" style="display:none" accept="image/x-png,image/gif,image/jpeg">
        <button class="btn float-right" v-on:click="$refs.avatar_edit.click()">
          <icon icon="edit" size="sm" />
        </button>
      </div>
      <div class="col"></div>
    </div>
    <div v-for="(info, index) in infos" v-bind:key="index" class="my-1">
      <div class="row">
        <div class="col mt-2">
          <span >{{ info.label }}</span>
        </div>
        <div class="col-8 mr-2">
          <div class="input-group input-group-info">
            <input
              :type="(index!=1)?'text':'date'"
              class="form-control input-info"
              disabled
              v-model="info.value"
              v-bind:id="'edit' + index"
              @change="update_profile"
            />
            <button
              class="btn input-group-append btn-edit"
              v-on:click="enable_input(index)"
            >
              <icon icon="edit" size="sm" />
            </button>
          </div>
        </div>
      </div>
    </div>
    <button class="btn btn-link px-0" v-on:click="$emit('logout')">{{translate("Logout",lang)}}</button>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faEdit } from "@fortawesome/free-solid-svg-icons";
import userRepository from '../../repositories/userRepository';
import { translate } from "../shared/translator";
library.add({ faEdit });
export default {
  data() {
    return {
      avatar_label: translate("avatar",this.lang),
      avatar_source:this.user.avatar,
      infos: [
        {
          label: translate("display_name",this.lang),
          value: this.user.name
        },
        {
          label: translate("birthday",this.lang),
          value: this.user.birthday
        },
        {
          label: translate("email",this.lang),
          value: this.user.email
        },
        {
          label: translate("contact",this.lang),
          value: this.user.contact
        }
      ]
    };
  },
  methods: {
    translate:translate,
    enable_input: function(index) {
      console.log(index);
      var input = document.getElementById("edit" + index);
      input.disabled = false;
      input.select();
    },
    change_avatar(e){
      let file = e.target.files[0] || e.dataTransfer.files[0];
      console.log(file);
      this.avatar_source=URL.createObjectURL(file);
      this.$emit("avatar_changed",file);
    },
    update_profile(){
      let updated_profile={
        name:this.infos[0].value,
        birthday:this.infos[1].value,
        gmail:this.infos[2].value,
        contact:this.infos[3].value,
      };
      this.$emit('profile_changed',updated_profile)
    }
  },
  props: {lang:{
      type:String,
      default:"en"
    },user:Object}
};
</script>

<style>
.avatar-profile {
  width: 200px;
  height: 200px;
  margin: auto;
}
.input-info {
  background-color: white !important;
  border-radius: 32px !important;
  border: none;
}
.input-group-info {
  background-color: white;
  /* margin:20px; */
  margin-top: 5px;
  margin-right: 10px !important;

  /* padding: 5px; */
  border-radius: 32px;
}
.btn-edit {
  border-radius: 32px !important;
}
</style>



