<template>
  <div class="nav">
    <div class="container-fluid">
      <div class="row">
        <div class="col-3">
          <span>
            <img class="logo mb-2" src="./logo.png" />
            <span class="brand mt-3">QDiary</span>
          </span>
        </div>
        <div class="col-6">
          <SearchBar :lang="lang" class="mt-2" @search="searchPost" />
        </div>
        <div class="col-3 mt-2">
          <!-- <div > -->

          <img v-on:click="change_active_tab('profile')" class="avatar" v-bind:src="user.avatar" />
          <span v-on:click="change_active_tab('profile')" class="username">{{ user.name }}</span>
          <button class="float-right nav-btn mt-1">
            <!-- </div> -->
            <icon v-on:click="change_active_tab('filter')" icon="filter" size="lg" class="icons" />
          </button>
          <button class="float-right nav-btn mt-1 mx-2">
            <icon v-on:click="change_active_tab('settings')" icon="cog" size="lg" class="icons" />
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchBar from "./SeachBar";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faFilter, faCog, faLanguage } from "@fortawesome/free-solid-svg-icons";
library.add(faFilter, faCog, faLanguage);
export default {
  data() {
    return {
      avatar_source:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcT_xTUvE4S36PXszCrf4ujpK47tF_zy19-QO_3jzZhSy2SAhlJP",
      name_character: "linh",
      username: "Trần Quang Linh"
    };
  },
  components: {
    SearchBar,
  },
  methods: {
    change_active_tab: function(tab_name) {
      this.$emit("myEvent", { tab_id: tab_name });
    },
    searchPost(e) {
      this.$emit("search", e);
    }
  },
  props: {
    lang: {
      type: String,
      default: "en"
    },
    user: Object
  }
};
</script>

<style>
.nav {
  background-color: black;
}
.avatar {
  width: 40px;
  height: 40px;
  margin: auto;
  border-radius: 32px;
  cursor: pointer;
}
.username {
  color: white;
  cursor: pointer;
}
.icons {
  color: white;
  /* width: 40px; */
  /* height: 40px; */
  /* margin: 10px; */
  /* padding: 2px; */
  cursor: pointer;
  /* border: solid 1px; */
  /* border-radius: 32px; */
}
.logo {
  width: 40px;
  height: 40px;
  margin: auto;
}
.brand {
  font-size: 30px;
  color: whitesmoke;
  font: Segoe UI;
}
.nav-btn {
  background-color: transparent;
  border: none;
  border-radius: 32px !important;
}
</style>



