<template>
  <div
    class="form-control card-topic mt-2 mborder"
    @click="updateTopic"
    v-bind:style="'background-image:url('+((topic.background!=undefined)?topic.background:background)+')'"
  >
    {{translate(topic.title,lang)}}
    <!-- <input
      type="checkbox"
      class="float-right"
      ref="topic_checkbox"
      v-model="checked"
      @change="updateTopicFilter($event)"
    />-->
    <CheckboxElement
      :disabled="true"
      ref="checkboxEl"
      class="float-right mr-4"
      v-model="checked"
      @change="updateTopicFilter"
    ></CheckboxElement>
  </div>
</template>

<script>
import { translate } from "../shared/translator";
import CheckboxElement from "../shared/CheckboxElement";
export default {
  components: {
    CheckboxElement
  },
  computed: {
    background() {
      switch (this.topic.topicID) {
        case 2:
          return "https://images.unsplash.com/photo-1474552226712-ac0f0961a954?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60";
        case 0:
          return "https://images.unsplash.com/photo-1535384515441-5a7293014fce?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60";
        case 1:
          return "https://images.unsplash.com/photo-1461896836934-ffe607ba8211?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60";
        case 3:
          return "https://images.unsplash.com/photo-1493225457124-a3eb161ffa5f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60";
        case 4:
          return "https://images.unsplash.com/photo-1517760444937-f6397edcbbcd?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60";
        default:
          return "https://i.pinimg.com/originals/cb/c2/2c/cbc22ca5a3d7568a742262639a9f6b3f.jpg";
      }
    }
  },
  data() {
    return {
      checked: this.choosed
    };
  },
  props: {
    topic: Object,
    lang: {
      type: String,
      default: "en"
    },
    choosed: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    translate: translate,
    updateTopicFilter(e) {
      console.log("changed", e);
      this.checked = e;
      this.$emit("changed", {
        checked: e,
        topicID: this.topic.topicID,
        title: this.topic.title
      });
    },
    updateTopic() {
      this.checked = !this.checked;
      this.$emit("changed", {
        checked: this.checked,
        topicID: this.topic.topicID,
        title: this.topic.title
      });
    },
    setChecked(checked) {
      // console.log("set checked active")
      this.checked = checked;
      // this.$refs['checkboxEl'].setChecked(checked)
    }
  }
};
</script>

<style>
.card-topic {
  height: 80pt;
  color: white;
  /* text-shadow: 2px; */
  font-size: 15pt;
  background-size: 500px;
  background-blend-mode: darken;
  -webkit-text-stroke-width: 0.1px;
  -webkit-text-stroke-color: rgb(0, 0, 0);
  padding-left: 20px;
  padding-top: 20px;
  padding-right: 20px;
  outline: none !important;
  background-position: center;
  cursor: pointer;
}
</style>


