<template>
  <div :class="show_icon?'row':''">
    <!-- <div class="col-auto"> -->
    <!-- <div class="row justify-content-start"> -->
    <span v-if="show_icon" class=" col-1 s-icon ml-1">
      <icon
        class="mt-2"
        :icon="icon"
        pull="left"
        size="lg"
      />
    </span>
    <div :class="show_icon?'col':''">
      <button
        :class="'btn btn-link '+(show_icon?'mx-2':'')"
        data-toggle="collapse"
        v-bind:data-target="'#id_' + icon + '_element'"
        v-bind:aria-expanded="is_expanded"
        v-bind:aria-controls="'collapse' + icon"
        v-on:click="is_expanded = !is_expanded"
      >
        {{ name }}
      </button>
      <!-- </div> -->
      <!-- </div> -->
      <!-- <div class="col-3"> -->
      <icon
        :class="'mt-2 '+((icon != 'language')?'mr-4':'mr-2')"
        v-bind:icon="!is_expanded ? 'angle-right' : 'angle-down'"
        pull="right"
        size="lg"
      />
    </div>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faComment,
  faLaptopCode,
  faBell,
  faLink,
  faUserShield,
  faSnowflake,
  faAngleRight,
  faAngleDown,
  faQuoteLeft,
  faGrinBeam,
  faLayerGroup,
  faAirFreshener,
  faTags
} from "@fortawesome/free-solid-svg-icons";
import { faThemeisle } from '@fortawesome/free-brands-svg-icons';

library.add(
  faLaptopCode,
  faBell,
  faLink,
  faUserShield,
  faAngleRight,
  faAngleDown,
  faSnowflake,
  faQuoteLeft,
  faGrinBeam,
  faThemeisle,
  faAirFreshener,
  faTags
);
export default {
  data() {
    return {
      is_expanded: false
    };
  },
  props: {
    name: String,
    icon: String,
    show_icon:Boolean
  }
};
</script>

<style>
.s-icon {
  width: 20px;
  height: 20px;
}
</style>



